import axios from 'axios'
import service from './api'
let root = process.env.url_api // process.env.url_api ''
// import { Indicator, Toast } from 'mint-ui'

// service
let instance = axios.create({
  baseURL: root, // http://menkouapp.sun7y.com http://117.78.28.26 http://lcg.momentinlet.com https://www.momentinlet.com http://t.momentinlet.com https://www.xcacc.net/
  timeout: 0
})
let instance2 = axios.create({
  baseURL: 'http://221.226.82.226:8003', // http://menkouapp.sun7y.com
  timeout: 0
})
const Http = {} // 

// /
for (let key in service) {
  let api = service[key] // url method
  // async
  Http[key] = async function (
    params, //  get,delete:urlput,post,patch:data
    isFormData = false, // form-data
    config = {} // 
  ) {
    let url = api.url
    let newParams = {}
    // loading
    if (api.mask) {
    } else {
    }
    // content-typeform-data
    let myIds = null // postisFormDatatrueinfo/1123myIdsids
    if (params && isFormData) {
      newParams = new FormData()
      for (let i in params) {
        if (api.detail && i === 'ids') {
          myIds = params.ids
        } else {
          newParams.append(i, params[i])
        }
      }
    } else {
      newParams = params
    }
    if (api.detail && newParams.ids) {
      // urlinfo/1123api.jsdetail: true
      url = url + newParams.ids
      delete newParams.ids
    } else if (api.detail && myIds) {
      url = url + myIds
    }
    // 
    let response = {} // 
    if (api.method === 'put' || api.method === 'post' || api.method === 'patch') {
      try {
        if (api.ins && api.ins === 'instance2') {
          response = await instance2[api.method](url, newParams, config)
        } else {
          response = await instance[api.method](url, newParams, config)
        }
      } catch (err) {
        response = err
      }
    } else if (api.method === 'delete' || api.method === 'get') {
      config.params = newParams // urldataconfigdata
      try {
        if (api.ins && api.ins === 'instance2') {
          response = await instance2[api.method](url, config)
        } else {
          response = await instance[api.method](url, config)
        }
      } catch (err) {
        response = err
      }
    }
    return response // 
  }
}

// 
// 
instance.interceptors.request.use(config => {
  // 
  config.headers.token = sessionStorage.token
  return config
}, () => {
  // 
  // Indicator.close()
  // Toast('~')
})
// 
instance.interceptors.response.use(res => {
  // 
  // Indicator.close()
  console.log(res.data)
  // 
  if (res.data && res.data.code && res.data.code === 401) {
    localStorage.removeItem('shopId')
    localStorage.removeItem('token')
    // window.location.replace('/#/login')
    window.location.replace('/login')
  }
  return res.data
}, () => {
  // 
  // Indicator.close()
  // Toast('~')
})

// 
// 
instance2.interceptors.request.use(config => {
  // 
  return config
}, () => {
  // 
  // Indicator.close()
  // Toast('~')
})
// 
instance2.interceptors.response.use(res => {
  // 
  // Indicator.close()
  return res.data
}, () => {
  // 
  // Indicator.close()
  // Toast('~')
})

export default Http



